<template>
	<view class="list_wrap">
		<view class="role_wrap flex-center">
			<view class="role_item" :class="current==0?'on':''" @click="roleSwitch(0)">
				<slot name='l_tit'></slot>
			</view>
			<view class="role_item" :class="current==1?'on':''" @click="roleSwitch(1)">
				<slot name='r_tit'></slot>
			</view>
		</view>
		<view class="pad-lr-30">
			<view class="flex-center flex-lr mar-t-20">
				<view class="option">
					<text class="f-26 color-1 ellipsis" @click="openAreaPicker(1)">{{start_name || '出发地'}}</text>
					<image src="../static/img/icon_close.png" class="icon_close" v-if="start_name" @click="clear(1)"></image>
					<image src="../static/img/icon_open.png" class="icon_open" v-else></image>
				</view>
				<view class="option">
					<text class="f-26 color-1 ellipsis" @click="openAreaPicker(2)">{{end_name || '目的地'}}</text>
					<image src="../static/img/icon_close.png" class="icon_close" v-if="end_name" @click="clear(2)"></image>
					<image src="../static/img/icon_open.png" class="icon_open" v-else></image>
				</view>
				<view class="option">
					<text class="f-26 color-1 ellipsis" @click="openTimePicker()">{{depart_time || '日期'}}</text>
					<image src="../static/img/icon_close.png" class="icon_close" v-if="depart_time" @click="clear(3)"></image>
					<image src="../static/img/icon_open.png" class="icon_open" v-else></image>
				</view>
			</view>
		</view>
		<view class="pad-lr-30">
			<swiper class="swiper" :current='current' @change="swiperChange">
				<swiper-item>
					<slot name="l-list"></slot>
				</swiper-item>
				<swiper-item>
					<slot name="r-list"></slot>
				</swiper-item>
			</swiper>
		</view>
		<areaPicker v-show='isShowArea' :valueArea='areaValue' @showChange='areaShowChange' />
		<timePicker v-if='isShowTime' :valueTime='timeValue' @showChange='timeShowChange' />
	</view>
</template>

<script>
	import areaPicker from './areaPicker.vue'
	import timePicker from './timePicker.vue'
	export default {
		name: 'list_wrap',
		components: {
			areaPicker,
			timePicker
		},
		props:['l_title','r_title'],
		data() {
			return {
				current: 0,
				areaType: '',
				areaValue: [0, 0, 0, 0],
				timeValue: [],
				areaDesc: '',
				isShowArea: false,
				isShowTime: false,
				startRole_1: {
					id: '',
					name: '',
					value: [0, 0, 0, 0],
				},
				startRole_2: {
					id: '',
					name: '',
					value: [0, 0, 0, 0],
				},
				endRole_1: {
					id: '',
					name: '',
					value: [0, 0, 0, 0],
				},
				endRole_2: {
					id: '',
					name: '',
					value: [0, 0, 0, 0],
				},
				departTime_1: {
					value: [],
					name: ''
				},
				departTime_2: {
					value: [],
					name: ''
				}

			}
		},
		computed: {
			start_name() {
				let name = ''
				if (this.current == 0) {
					name = this.startRole_1.name
				} else {
					name = this.startRole_2.name
				}
				console.log(name)
				return name
			},
			end_name() {
				let name = ''
				if (this.current == 0) {
					name = this.endRole_1.name
				} else {
					name = this.endRole_2.name
				}
				return name
			},
			depart_time() {
				let time = ''
				if (this.current == 0) {
					time = this.departTime_1.name
				} else {
					time = this.departTime_2.name
				}
				return time
			}
		},
		methods: {
			roleSwitch(current) {
				this.current = current
			},
			swiperChange(e) {
				this.current = e.detail.current
			},
			//暂时没用
			// areaPickerChange(value, desc) {
			// 	this.areaDesc = desc
			// 	this.areaValue = value
			// },
			areaShowChange(flag, value, id, name) {
				let data = {
					value,
					id,
					name
				}
				this.isShowArea = false
				if (flag == 'SURE') {
					this.setTypeParam(data, this.areaType)
				}
			},
			timeShowChange(flag, value, name) {
				this.isShowTime = false
				if (flag == 'SURE') {
					this.setTypeParam({value, name}, 3)
				}
			},
			//type 1 start 2 end
			openAreaPicker(type) {
				this.areaType = type
				this.areaValue = this.getTypeParam(type).value
				this.isShowArea = true

			},
			openTimePicker() {
				this.timeValue = this.current == 0 ? this.departTime_1.value : this.departTime_2.value
				this.isShowTime = true
			},
			clear(type) {
				if (type == 3) {
					let data = {
						value: [],
						name: ''
					}
					this.setTypeParam(data, type)
				} else {
					let data = {
						value: [0, 0, 0, 0],
						name: '',
						id: ''
					}
					this.setTypeParam(data, type)
				}
			},
			setTypeParam(data, type) {
				if (this.current == 0) {
					if (type == 1) {
						this.startRole_1 = data
					} else if (type == 2) {
						this.endRole_1 = data
					} else {
						this.departTime_1 = data
					}
				} else {
					if (type == 1) {
						this.startRole_2 = data
					} else if (type == 2) {
						this.endRole_2 = data
					} else {
						this.departTime_2 = data
					}
				}
				this.$nextTick(() => {
					let start_id = this.current == 0 ? this.startRole_1.id : this.startRole_2.id
					let end_id = this.current == 0 ? this.endRole_1.id : this.endRole_2.id
					let depart_time = this.current == 0 ? this.departTime_1.name : this.departTime_2.name
					this.$emit('refresh', this.current, start_id, end_id, depart_time)
				})
			},

			getTypeParam(type) {
				let obj = {}
				if (this.current == 0) {
					if (type == 1) {
						obj = this.startRole_1
					} else {
						obj = this.endRole_1
					}
				} else {
					if (type == 1) {
						obj = this.startRole_2
					} else {
						obj = this.endRole_2
					}
				}
				return obj
			}
		}
	}
</script>

<style lang="less">
	.swiper {
		height: calc(100vh - 200rpx);
	}

	.list_wrap {
		.role_wrap {
			width: 100%;
			height: 88rpx;
			background: #FFFFFF;
			margin-top: 10upx;

			.role_item {
				width: 50%;
				text-align: center;
				font-size: 28rpx;
				font-weight: 500;
				color: #848484;
				line-height: 36rpx;

				&.on {
					font-size: 36rpx;
					font-weight: bold;
					color: #1A1A1A;
					line-height: 36px;

					text {
						position: relative;

						&:after {
							content: '';
							display: block;
							width: 145rpx;
							height: 28rpx;
							background: linear-gradient(0deg, rgba(#0493FF, 0.56), rgba(#0493FF, 0.1));
							position: absolute;
							bottom: 0;
							z-index: 6;
							left: 0;
							right: 0;
							margin: 0 auto;
						}

					}
				}
			}
		}

		.option {
			width: 176rpx;
			height: 66rpx;
			background: #FFFFFF;
			border-radius: 10rpx;
			line-height: 66rpx;
			padding: 0 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			image {
				flex-shrink: 0;
			}

			text {
				flex-grow: 1;
			}
		}
	}
</style>
